/* Estilo para la scrollbar (solo va en Chrome, pero mejor que nada) */

::-webkit-scrollbar {
    width: 11px;
    height: 11px;
}

::-webkit-scrollbar-button {
    width: 0px;
    height: 0px;
}

::-webkit-scrollbar-thumb {
    background: #17a2b8;
    border: 3px inset #46c0ce;
    border-radius: 52px;
}

::-webkit-scrollbar-thumb:hover {
    background: #2bdbd7;
}

