    background: #2bdbd7;
}

::-webkit-scrollbar-track {
    background: #9e9e9e;
    border: 0px dotted #ffffff;
    border-radius: 23px;
}

::-webkit-scrollbar-track:hover {
    background: #9e9e9e;
}

::-webkit-scrollbar-track:active {
    background: #9e9e9e;
}

::-webkit-scrollbar-corner {
    background: transparent;
}
