.mr-auto {
    margin: auto;
}

.icon {
    margin-right: 0.2em;
    color: rgb(97, 208, 228);
}

.opcionYLogo {
    display: flex;
    align-items: center;
    padding: 0em 0.9em;
    background: #343a40;
}

.dropdown {
    color: white !important;
    text-decoration: none;
    font-size: 1.0em;
    background: #343a40;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.dropdown:hover {
    text-decoration: none;
    color: rgb(117, 227, 247);
    font-size: 1.05em;
    background: #343a40;
}

#dropdownItemL, .optionL, .navBarLogout .logout, .nav-link {
    color: white !important;
    text-decoration: none;
    font-size: 1.1em;
    background: #343a40;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.optionL:hover, .navBarLogout .logout:hover {
    text-decoration: none;
    color: rgb(117, 227, 247);
    font-size: 1.15em;
}

.optionL:active, .navBarLogout .logout:active {
    color: rgb(60, 130, 143);
    font-size: 1.2em;
}

.logout {
    background: transparent;
    border: none;
    color: rgb(117, 227, 247) !important;
    text-decoration: none;
    font-size: 1.1em;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.dropdown-item { background-color: #343a40 !important; }

.dropdown-menu{
    background-color: rgb(96, 194, 211) !important;
}